body {
	min-width: 960px;
}

.page-wrap {
	min-width: 960px;
}

.flex-row {
	display: table;
	width: 100%;
	table-layout: fixed;

	> div {
		display: table-cell;
		vertical-align: top;
	}
}


.page-wrap {
	display: block;
}

.header-row {
	display: block;
}

.layout-row {
	display: block;
}

.l-header {
	table-layout: auto;
}



.l-layout {
	display: table;
	width: 100%;
}

.l-aside {
	display: table-cell;
	vertical-align: top;
	width: 300px;
}

.l-main {
	display: table-cell;
	vertical-align: top;
}


.h-topline {
	display: table;
	width: 100%;
}

.topline-nav {
	display: table-cell;
}

.topline-aside {
	display: table-cell;
	width: 390px;
	text-align: right;
}

.topline-iblogin {
	display: inline-block;
	vertical-align: middle;
}

.topline-search {
	display: inline-block;
	vertical-align: middle;
}

.topline-lang {
	display: inline-block;
	vertical-align: middle;
}


.h-topmenu {
	ul {
		height: 70px;
		display: block;
		margin-left: 40px;
		list-style: none;
	}

	li {
		display: inline-block;
		vertical-align: top;
		height: 70px;
		box-sizing: border-box;

		&:nth-child(1) {
			max-width: 160px;
		}

		&:nth-child(2) {
			max-width: 150px;
		}

		&:nth-child(3) {
			max-width: 240px;
		}

		&:nth-child(4) {
			max-width: 190px;
		}

		&:nth-child(5) {
			max-width: 230px;
		}

		&:nth-child(6) {
			max-width: 150px;
		}

		&.flexMenu-viewMore {
			width: 50px;

			> a {
				display: block;

			}
		}
	}

	.topmenu-popup {
		display: block;

		li {
			display: table;
			width: 100%;
			height: 70px;
			width: 250px;
		}

		a {
			display: block;
			width: 100%;
		}
	}
}

.topmenu-link {
	display: block;

	font-weight: 400;
	font-size: 20px;
	line-height: 1.1;
	color: #000;
	padding: 0.75rem 30px 0;
	height: 70px;
	text-decoration: none;
	box-sizing: border-box;

	&:hover {
		color: #fff;
	}

	&.active {
		color: #fff;
	}
}




.card {
	display: block;

	.card-node {
		display: block;
	}
}





.l-popular {
	padding: 20px;

	.maxwrap {
		display: block;

		&:after {
			content: "";
			display: block;
			clear: both;
		}
	}

	.popular-col {
		display: block;
		width: 25%;
		float: left;
		box-sizing: border-box;
		padding: 10px;

		&:first-child {
			width: 50%;
		}

		.card {
			height: 250px;
			margin: 10px;

			.card-node:last-child {
				height: 90px;
				position: absolute;
				bottom: 20px;
			}
		}
	}

}



.l-products-top {

	padding: 20px;

	.maxwrap {
		display: block;

		&:after {
			content: "";
			display: block;
			clear: both;
		}
	}

	.products-col {
		display: block;
		width: 25%;
		float: left;
		box-sizing: border-box;
	}

	.box {
		padding: 1.5rem;
		height: 325px;
		min-height: auto;
		box-sizing: border-box;
	}

	.card-circular {
		border-radius: 50%;
		overflow: hidden;
		// transform: scale(0.8);
		transform-origin: 100% 100%;
	}
}






















